
import back from '@/assets/icons/arrow-back.svg'

export default {
    name: 'ConversationDetails',
    props: {
        conversation: Object
    },
    data () {
        return {
            back
        }
    },
    methods: {
        onCloseRequest () {
            this.$refs.page.classList.add('slide-rtl-return')
            setTimeout(() => {
                this.$emit('onCloseRequest')
            }, 300)
        }
    }
}
