
export default {
  name: 'ServiceCategory',
  data () {
    return {
      screenWidth: window.innerWidth
    }
  },
  mounted () {
    window.addEventListener('resize', this.onResize)
  },
  beforeUnmount () {
    removeEventListener('resize', this.onResize)
  },
  methods: {
    onResize () {
      this.screenWidth = window.innerWidth
    }
  }
}
