
export default {
    name: 'FileInput',
    props: {
      label: String,
      class: String,
      hidden: {
        type: Boolean,
        default: false
      }
    },
    data () {
      return {
        file: undefined,
      }
    },
    emits: ['onFileSelected'],
    methods: {
      handleFileSelection: function (e) {
        const file = e.target.files[0]
        this.file = file
        this.$emit('onFileSelected', file) 
      }
    }
}
